@font-face {
  font-family: MyriadPro;
  src: url('../fonts/MyriadPro-Regular.otf') format('opentype'); }

@font-face {
  font-family: TacaPro-Bold;
  src: url('../fonts/TacaPro-Bold.otf') format('opentype');
