@import "colors";
@import "fonts";
@import "helpers";

body {
  background-color: $white;
  margin-top: 80px;
  position: relative; }

h2 {
  font-family: 'TacaPro-Light';
  font-size: 3em;
  text-transform: uppercase;
  color: $blue;
  margin-top: 50px;
  margin-left: 50px;
  margin-bottom: 30px; }

.navbar {
  &.navbar-inverse {
    background-color: $black;
    border-color: $black;
    min-height: 80px;
    & .navbar-nav {
      &>li>a {
        font-family: 'MyriadPro';
        font-size: 1.1em;
        padding-left: 7px;
        padding-right: 7px;
        padding-top: 65px;
        padding-bottom: 0px;
        line-height: 0px; }
      & .active a {
        color: $blue;
        background-color: $black; } } }
  & .navbar-brand {
    padding: 0 15px;
    height: 80px;
    line-height: 80px;
    &>img {
      height: 100%;
      padding: 5px;
      width: auto; } }
  &.navbar-toggle {
    margin-top: 23px;
    padding: 9px 10px !important; }
  & .open > .dropdown-menu {
    display: inline-block; }
  & .dropdown:hover .dropdown-menu {
    display: block; }
  & .dropdown-menu {
    margin-top: 0px;
    font-family: 'TacaPro-Regular';
    background-color: $black;
    width: 670px;
    left: 0;
    margin-top: 5px !important;
    & ul {
      list-style-type: none; }
    & li {
      float: left;
      width: 20%;
      background-color: $black;
      -moz-transition: opacity 0.4s ease-in-out;
      -o-transition: opacity 0.4s ease-in-out;
      -webkit-transition: opacity 0.4s ease-in-out;
      transition: opacity 0.4s ease-in-out;
      &.active-category a {
        opacity: 1; }
      & a {
        opacity: 0.4;
        background-color: $black;
        padding: 0;
        margin: 3px;
        &:hover {
          opacity: 1; } }
      & p {
        color: $light_gray;
        text-align: center;
        margin-bottom: 3px; } } } }

