      background: -ms-linear-gradient(left, rgba(0,0,0,1) 0%, rgba(0,0,0,0.91) 9%, rgba(3,3,3,0.76) 24%, rgba(0,0,0,0.6) 40%, rgba(0,0,0,0.52) 48%, rgba(3,3,3,0.5) 50%, rgba(3,3,3,0.39) 61%, rgba(5,5,5,0.25) 75%, rgba(3,3,3,0.12) 88%, rgba(3,3,3,0) 100%);
      background: linear-gradient(to right, rgba(0,0,0,1) 0%, rgba(0,0,0,0.91) 9%, rgba(3,3,3,0.76) 24%, rgba(0,0,0,0.6) 40%, rgba(0,0,0,0.52) 48%, rgba(3,3,3,0.5) 50%, rgba(3,3,3,0.39) 61%, rgba(5,5,5,0.25) 75%, rgba(3,3,3,0.12) 88%, rgba(3,3,3,0) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#030303', GradientType=1 );
      & span {
